import React, { useState } from "react";
import useDocument from "../Composables/useDocument";
import { timestamp } from "../Firebase/config";
import {useHistory} from 'react-router-dom';
import useStorage from '../Composables/useStorage'
const AddBook = (props) => {

    const [book, setBook] = useState({
       author:"",
       categroy: "fantasy",
       detail: "",
       title: "",
       img : ""

    });

   const history = useHistory();
    const { add , loading} = useDocument("book");
    //const categroys = ["fantasy","roman","erindringer","udlandet","drama"]
    const {url, uploadImage, loadingUpload} = useStorage("book");
    const [file,setFile] = useState(null)
    const types = ['image/png', 'image/jpeg']
    const [fileError,setFileError] = useState(null)
    const [uploadMessage,setUpMessage] = useState(null)
    function handleChange(e){
        
        const {name , value} = e.target;
        
        setBook((prevBook) => {
            return {
                ...prevBook,
                [name]: value
            }
        })
      };

      function handleUpdate(e){
     
            let selected = e.target.files[0]
            console.log(selected)
            if(selected && types.includes(selected.type)){
                setFile(selected)
                
                setFileError(null)
            } else{
                setFile(null)
                setFileError('please select an image file(png or jpg)')
            }
         
        }

        const handleClick = async (e) => {
            e.preventDefault();
            console.log(file);
            if (file) {
              await uploadImage(file);
              setUpMessage("du har add et nyt billede")
            }
          };
           

      const handleSubmit = (e) => {    
        e.preventDefault()
        console.log(url)
        add({ author: book.author,
        categroy: book.categroy,
        detail: book.detail,
        title:  book.title,
        img: !url ? "https://static.thenounproject.com/png/2451944-200.png" : url,
        createdAt: timestamp() });
        
        setBook({
            author:"",
            categroy: "fantasy",
            detail: "",
            title: "",
            img: "https://static.thenounproject.com/png/2451944-200.png"
         });
        props.setMessage("add")
        history.push('/')
      };

    return ( <div>
        <h1 className="book-headline">Add Book</h1>
        <div className="addbook">
            <form onSubmit={handleSubmit}>
                
                <div className="addbook-upload">
                   Img: <input name="img" type="file" onChange={handleUpdate} /> 
                    <button className="btn btn-secondary" onClick={(e) => {handleClick(e)}} > upload </button>
                </div>
                {uploadMessage && <div>{uploadMessage}</div>}
                <div className="addbook-form" >
            Title: <input type="text"  value={book.title} name="title"  onChange={handleChange}/>
            Info: <textarea type="text" rows="4" value={book.detail} name="detail"  onChange={handleChange}/>
            Categroy: <select name="categroy" value={book.categroy}onChange={handleChange}>
                        {props.categroys.map((categroy,index)=>(
                            <option name={categroy} key={index}>{categroy}</option>
                            ))}
                        </select>
            Author:  <input type="text" value={book.author} name="author"  onChange={handleChange}/>
           
            {!loading && <button className="btn btn-secondary" > add </button>}
            {loading && <button disabled>Adding Book....</button>}
            {fileError && <div>{fileError}</div>}
            {loadingUpload && <div>Waiting for photo</div>}
            </div>
             </form>
             </div>
     
      
    </div> );
}
 
export default AddBook;