import { useHistory, useParams } from "react-router";
import {useState} from "react"
import useGetItem from "../Composables/useGetItem";
import useDocument from "../Composables/useDocument";
import EditBooks from "./EditBooks"

const BlogDetails = (props) => {
    const { id } = useParams()
    let {item: book, error} = useGetItem('book', id)
    const { deleteDoc,update } = useDocument("book",id)
    const [show, setShow] = useState(false);
   


      // console.log(item)
    const history = useHistory()
    const handleClick = (info) => {
        if(info === "delete"){
            if(id){
                 deleteDoc(id)
                 history.push('/')
            }
        } else if(info === "edit"){
            setShow(!show);
        }
    }
    return (  
        <div className="book-details">
           {error && <div>{error}</div>}
           {book && (
               <article>
                    <img src={book.img} className="img" alt="books" />
                   <div className="book-details-text">
                   <h2>{book.title}</h2>
                   <h3>info</h3>
                    <p>{book.detail}</p>
                    <h3>Written by </h3>
                   <p>{book.author}</p>
                   <h3>Categroy </h3>
                   <p>{book.categroy}</p>
                   </div>
                   <button onClick={() => handleClick("edit")}>Charge</button>
                   <button onClick={() => handleClick("delete")}>Delete</button>
                  
                   {show && (
                       <EditBooks book={book} update={update} id={id} categroys={props.categroys} setMessage={props.setMessage}/>
                    )}
                </article>
           )}
           {error && <div>Error...</div>}
        </div>
    );
}
 
export default BlogDetails;