import {Link} from 'react-router-dom';

const Navbar = () => {
    return (  <div className="navbar">
        <Link to="/">
        <h1>Lyngby Biblotik</h1>
        </Link>
        <div className="links">
                <Link to="/">Home</Link>
                <Link to="/addbook">Add Book</Link>
            </div>
        
    </div>);
}
 
export default Navbar;