import { Link } from "react-router-dom"
import {useState} from 'react'
import Book from './Book'
import LeftBar from './LeftBar'
const BookList = (props) => {

  const [books,setBooks] = useState(props.books)
 
  
  const filteredProjects = (event) => {
    const newBooks = props.books;
    const {innerHTML} = event.target
    setBooks(newBooks)
    if(innerHTML !== "All"){
      let fitlerBooks = newBooks.filter(book => book.categroy ===  innerHTML)
      setBooks(fitlerBooks)
    } 
    
  }



    return (

      <div className="layout-wrapper row">
        <LeftBar categroys={props.categroys} onShow={filteredProjects}/>
        <div className="column">
        <h2>Litteratur</h2>
    {books.map((book) => (
            <div className="book" key={book.id} >
            <Link to={`/book/${book.id}` }>
            <Book book={book}/>
            </Link>  
            </div>
        ))}
        </div>
      </div>
    )
   
}
 
export default BookList;