
import useFetch from '../Composables/useFetch'


import Booklist from './Booklist'
const Home = (props) => {
    const { data: books, isPending, error } = useFetch("book");
    

    if(!isPending) {
    return ( 
     <div>
          {props.message === "add" && <h3 className="book-headline"> New Book releases </h3>}
          {props.message === "edit" && <h3 className="book-headline"> Edit Book</h3>}
        <h1 className="book-headline">
                E-bøger
        </h1>
        
            {error && <div>{error}</div>}
                {books && <Booklist books={books} categroys={props.categroys}/>} 
    </div>    
    );
    } else{
        return(
            <div>Loading....</div>
        )
    }
}
 
export default Home;