const LeftBar = ({categroys,onShow}) => {

    return ( 
        
        <aside className="column-leftbar">
            <div className="pane-content">
                <h2>Kategorier</h2> 
                <ul >
                <li value="all" onClick={onShow} >All</li>
                {categroys.map((categroy,index) => (
                  <li value={categroy} key={index} onClick={onShow}>{categroy}</li>
                ))}
            </ul>
            </div>
        </aside>
     );
}
 
export default LeftBar;