
import Navbar from './Navbar';
import Home from './Home';
import NotFound from './NotFound';
import AddBook from './AddBook';
import BookDetails from './BookDetails';
import { useState } from "react";



import {BrowserRouter as Router, Route,Switch} from 'react-router-dom';
function App() {

  const [message, setMessage] = useState("")
  const categroys = ["fantasy","roman","erindringer","udlandet","drama"]

  return (
    <Router>
    <div className="App">
     <Navbar/>
     <div className="content">
     <Switch>
     
        <Route exact path="/">
          <Home categroys={categroys} message={message}/>
        </Route>
          <Route path="/addbook">
             <AddBook setMessage={setMessage} categroys={categroys}/>
          </Route>
          <Route path="/book/:id">
              <BookDetails categroys={categroys}  setMessage={setMessage}/>
            </Route>
          <Route path="*">
              <NotFound />
          </Route>
      </Switch>
      </div>
    </div>
    </Router>
  );
}

export default App;
