import {useState} from "react"
import {useHistory} from 'react-router-dom';
const EditBooks = (props) => {
    const history = useHistory()
    const book = props.book
    const [item,setItem] = useState(book)
    const handleChange = (event) =>  {   
        const {name , value} = event.target;
      
        setItem((prevBook) => {
               return {
               ...prevBook,
               [name]: value
               }
           })
       };
      const handleSubmit = (e) =>{
        e.preventDefault();
        props.update(item.id,item)
        props.setMessage("edit")
        history.push('/')
      }

    return ( <div className="editbook">
                   <form   onSubmit={handleSubmit}>
                Title: <input type="text"  value={item.title} name="title"  onChange={handleChange}/>
                Info: <textarea type="text" rows="4" value={item.detail} name="detail"  onChange={handleChange}/>
                Categroy: <select name="categroy" value={item.categroy} onChange={handleChange}>
                        {props.categroys.map((categroy,index)=>(
                            <option name={categroy} key={index}>{categroy}</option>
                            ))}
                        </select>
                Author:  <input type="text" value={item.author} name="author"  onChange={handleChange}/> 
            <button>Edit</button>
        </form>
   </div> );
}
 
export default EditBooks;